import NoChartDataImg from '@/assets/images/no_chart_data@2x.png'
import LiquidityRangeChart from '@/components/liquidity/clmm/LiquidityRangeChart'
import useGetCurrentPrice from '@/hooks/clmm/useGetCurrentPrice'
import useGetTicksChartData from '@/hooks/clmm/useGetTicksChartData'
import useLiquidityStore from '@/store/clmm'
import usePriceRangeStore from '@/store/clmm/priceRange'
import usePositionStore from '@/store/position'
import usePositionDetailStore from '@/store/position/detail'
import { PoolApiInfo } from '@/types'
import { getReversePrice } from '@/utils/pool'
import { formatNumberWithDown } from '@cetus/utils'
import { d, TickMath, TickUtil } from '@cetusprotocol/common-sdk'
import { Box, Flex, Image } from '@chakra-ui/react'
import { useDeepCompareEffect } from 'ahooks'
import { useEffect, useMemo } from 'react'

function ChartBlock({ direct, minPriceForDate, maxPriceForDate }: { direct: boolean; minPriceForDate: any; maxPriceForDate: any }) {
  const { currentPosBaseInfo } = usePositionStore()
  const { currentPosPoolInfo, curPosContractPoolInfo } = usePositionDetailStore()
  const { setContractPoolInfo, setApiPoolInfo } = useLiquidityStore()
  const { lowerTickData, upperTickData, setLowerTickData, setUpperTickData } = usePriceRangeStore()

  useDeepCompareEffect(() => {
    setApiPoolInfo(currentPosPoolInfo)
  }, [currentPosPoolInfo])

  const handleSetTickData = (tick: number, type: 'lower' | 'upper', poolApiInfo: PoolApiInfo) => {
    const decimalsA = poolApiInfo?.tokenA?.decimals
    const decimalsB = poolApiInfo?.tokenB?.decimals
    let price

    if (Math.abs(tick) == Math.abs(TickUtil.getMinIndex(Number(poolApiInfo.tickSpacing)))) {
      price = type === 'lower' ? '0' : '‚àû'
    } else {
      price = TickMath.tickIndexToPrice(tick, decimalsA, decimalsB).toString()
    }

    const displayPrice = price === '‚àû' ? '‚àû' : formatNumberWithDown(price, 6)
    const reversePrice = getReversePrice(price)
    const displayReversePrice = reversePrice === '‚àû' ? '‚àû' : formatNumberWithDown(reversePrice, 6)

    const data = {
      id: type,
      tokenA: poolApiInfo?.tokenA,
      tokenB: poolApiInfo?.tokenB,
      tick,
      price,
      displayPrice,
      reversePrice,
      displayReversePrice,
      tickSpacing: poolApiInfo?.tickSpacing,
      pool: poolApiInfo?.poolAddress
    }

    if (type === 'lower') {
      setLowerTickData({ ...data })
    } else {
      setUpperTickData({ ...data })
    }
  }

  const { getCurrentPrice } = useGetCurrentPrice()

  useEffect(() => {
    if (currentPosBaseInfo?.lowerTick && curPosContractPoolInfo?.current_sqrt_price && currentPosPoolInfo?.poolAddress) {
      handleSetTickData(currentPosBaseInfo?.lowerTick, 'lower', { ...currentPosPoolInfo, tickSpacing: curPosContractPoolInfo.tickSpacing })
      handleSetTickData(currentPosBaseInfo?.upperTick, 'upper', { ...currentPosPoolInfo, tickSpacing: curPosContractPoolInfo.tickSpacing })

      getCurrentPrice(curPosContractPoolInfo?.current_sqrt_price, currentPosPoolInfo, curPosContractPoolInfo?.current_tick_index)
      setContractPoolInfo(curPosContractPoolInfo)
    }
  }, [currentPosBaseInfo?.lowerTick, curPosContractPoolInfo?.current_sqrt_price, currentPosPoolInfo?.poolAddress])

  const canDisplay = useMemo(() => {
    return currentPosBaseInfo?.posId && lowerTickData?.pool && upperTickData?.pool
  }, [lowerTickData?.pool, upperTickData?.pool, currentPosBaseInfo?.posId])
  // console.log('üöÄ ~ ChartBlock ~ currentPosBaseInfo?.isReverse:', currentPosBaseInfo?.isReverse)

  const { isPosDetailRefresh } = usePositionDetailStore()
  const { contractPoolInfo, apiPoolInfo } = useLiquidityStore()
  const { getFormattedData } = useGetTicksChartData()

  useEffect(() => {
    if (isPosDetailRefresh) {
      if (contractPoolInfo?.poolAddress && contractPoolInfo?.poolAddress === apiPoolInfo?.poolAddress) {
        console.log('Test Depth Data###LiquidityRangeChart contractPoolInfo: ', contractPoolInfo)
        console.log('Test Depth Data###LiquidityRangeChart apiPoolInfo1: ', apiPoolInfo)
        console.log('Test Depth Data###LiquidityRangeChart contractPoolInfo?.poolAddress: ', contractPoolInfo?.poolAddress)
        getFormattedData({
          poolAddress: contractPoolInfo?.poolAddress,
          tokenA: apiPoolInfo?.tokenA,
          tokenB: apiPoolInfo?.tokenB,
          tickSpacing: contractPoolInfo?.tickSpacing,
          currentTickIndex: contractPoolInfo?.current_tick_index,
          liquidity: contractPoolInfo?.liquidity,
          // feeRate: contractPoolInfo?.fee_rate
          feeRate: d(apiPoolInfo?.feeRate).mul(100).toString() || contractPoolInfo?.fee_rate
        })
      }
    }
  }, [isPosDetailRefresh, contractPoolInfo?.poolAddress, apiPoolInfo?.poolAddress])

  return (
    <Box className="chart-block" w="260px" h="104px" mb="8px" overflow="hidden">
      {canDisplay ? (
        // <LiquidityRangeChart
        //   isFrom="position"
        //   handleClickRefresh={() => {}}
        //   minPriceData={!currentPosBaseInfo?.isReverse ? lowerTickData : upperTickData}
        //   maxPriceData={!currentPosBaseInfo?.isReverse ? upperTickData : lowerTickData}
        //   direct={!currentPosBaseInfo?.isReverse}
        //   readonly={true}
        // />
        <LiquidityRangeChart
          isFrom="position"
          handleClickRefresh={() => {}}
          // direct={!currentPosBaseInfo?.isReverse}
          direct={direct}
          minPriceData={direct ? lowerTickData : upperTickData}
          maxPriceData={direct ? upperTickData : lowerTickData}
          dashedMarkerLine={[minPriceForDate, maxPriceForDate]}
          readonly={true}
        />
      ) : (
        <Flex justify="center" align="center">
          <Image src={NoChartDataImg} w="100px" h="100px" />
        </Flex>
      )}
    </Box>
  )
}
export default ChartBlock
