import WithTooltipInfo from '@/components/common/WithTooltipInfo'
import usePosHelper from '@/hooks/position/usePosHelper'
import usePositionStore from '@/store/position'
import { PosBaseInfo, showNewVersionApr } from '@/types'
import { SelectTab } from '@cetus/design'
import { Tab } from '@cetus/design/src/components/common/SelectTab'
import useTokenPrice from '@cetus/hooks/src/useTokenPrice'
import { Token } from '@cetus/types'
import { formatNumber, formatPrice, isAvailableObject, textEllipses } from '@cetus/utils'
import { d, TickMath } from '@cetusprotocol/common-sdk'
import { Center, Divider, HStack, Skeleton, Text, VStack } from '@chakra-ui/react'
import { useEffect, useMemo, useState } from 'react'
import PositionCurrentPrice from '../../common/PositionCurrentPrice'
import ChartBlock from './ChartBlock'

function PositionChartInfo({ positionApr, isAprLoading }: { positionApr: any; isAprLoading: boolean }) {
  const { currentPosBaseInfo, posPoolsRelatedData, posPoolsOriginalData, poolRangeObj, posApiPoolData, posLiquidityData } = usePositionStore()
  const posPoolInfo = posApiPoolData[currentPosBaseInfo?.clmmPool as string]
  const currentPosPoolsRelatedData = posPoolsRelatedData[currentPosBaseInfo?.posId as string]

  const leverage = useMemo(() => {
    console.log('üöÄ ~ leverage ~ currentPosPoolsRelatedData:', currentPosPoolsRelatedData)
    const minPrice = currentPosPoolsRelatedData?.minPriceOrigin
    const maxPrice = currentPosPoolsRelatedData?.maxPriceOrigin
    if (minPrice !== '0' && maxPrice !== '‚àû') {
      return formatNumber(1 / (1 - Number((minPrice / maxPrice) ** 0.25)), 2) + 'x'
    } else {
      return '1x'
    }
  }, [currentPosPoolsRelatedData?.minPriceOrigin, currentPosPoolsRelatedData?.maxPriceOrigin])

  const rangeTabList = useMemo(() => {
    if (currentPosBaseInfo) {
      return [currentPosBaseInfo?.displayTokenA, currentPosBaseInfo?.displayTokenB]?.filter(Boolean).map(item => ({
        label: item?.symbol,
        key: item?.coin_type,
        isToken: true,
        imgInfo: {
          src: item?.logo_url,
          w: '16px',
          h: '16px',
          coinType: item ? item?.coin_type : '',
          showTagWidth: '8px',
          showTagHeight: '8px'
        }
      }))
    } else {
      return []
    }
  }, [currentPosBaseInfo?.displayTokenA?.symbol, currentPosBaseInfo?.displayTokenB?.symbol])
  const [currentRangeTab, setCurrentRangeTab] = useState<string>()

  // ÂàùÂßãÂåñÂΩìÂâçÈÄâ‰∏≠coinÊ†áÁ≠æ Initialize current coin tab
  useEffect(() => {
    if (currentPosBaseInfo) {
      const coinType = currentPosBaseInfo?.displayTokenA?.coin_type
      setCurrentRangeTab(coinType)
    }
  }, [currentPosBaseInfo?.clmmPool])

  // ËÆ°ÁÆóÊñπÂêë Calculate direction
  const direct = useMemo(() => {
    return currentRangeTab === currentPosBaseInfo?.tokenA?.coin_type
  }, [currentRangeTab, currentPosBaseInfo?.tokenA?.coin_type])

  const [isChangeDirect, setIsChangeDirect] = useState<undefined | boolean>(undefined)
  // Â§ÑÁêÜtabÂèçËΩ¨ÁÇπÂáª Handle tab reverse click
  const onReverseClick = (item?: Tab) => {
    setIsChangeDirect(isChangeDirect == undefined ? false : !isChangeDirect)
    if (item && item?.coin_type) {
      setCurrentRangeTab(item?.coin_type)
    } else {
      setCurrentRangeTab(rangeTabList?.find(tab => tab.key !== currentRangeTab)?.key)
    }
  }
  // ËÆ°ÁÆó‰ª∑Ê†ºÊòæÁ§∫ÊñáÊú¨ Calculate price display text
  const perText = useMemo(() => {
    if (!currentPosBaseInfo?.tokenA || !currentPosBaseInfo?.tokenB) return ''
    return direct
      ? `${textEllipses(currentPosBaseInfo?.tokenB?.symbol, 10)} per ${textEllipses(currentPosBaseInfo?.tokenA?.symbol, 10)}`
      : `${textEllipses(currentPosBaseInfo?.tokenA?.symbol, 10)} per ${textEllipses(currentPosBaseInfo?.tokenB?.symbol, 10)}`
  }, [direct, currentPosBaseInfo?.tokenA as Token, currentPosBaseInfo?.tokenB as Token])

  const [tab, setTab] = useState({ type: '30D', key: 'month' })
  const [minPriceForDate, setMinPriceForDate] = useState('')
  const [maxPriceForDate, setMaxPriceForDate] = useState('')
  useEffect(() => {
    let _min = ''
    let _max = ''
    if (isAvailableObject(poolRangeObj) && isAvailableObject(currentPosBaseInfo) && isAvailableObject(tab)) {
      const rangArr = poolRangeObj[currentPosBaseInfo?.clmmPool as string]?.ranges
      console.log('üöÄüöÄüöÄ ~ PositionChartInfo.tsx:94 ~ useEffect ~ rangArr:', rangArr)
      if (!rangArr) return
      const rangesWithDateTypeMap = Object.fromEntries(
        rangArr?.map((item: any) => [
          item?.dateType,
          [
            TickMath.tickIndexToPrice(
              item?.lower,
              currentPosBaseInfo?.tokenA!.decimals as number,
              currentPosBaseInfo?.tokenB!.decimals as number
            ).toString(),
            TickMath.tickIndexToPrice(
              item?.upper,
              currentPosBaseInfo?.tokenA!.decimals as number,
              currentPosBaseInfo?.tokenB!.decimals as number
            ).toString()
          ]
        ])
      )
      const lowest = (rangesWithDateTypeMap as any)[tab?.key]?.[0]
      const lowValue = direct ? lowest : d(1).div(lowest).toString()
      const highest = (rangesWithDateTypeMap as any)[tab?.key]?.[1]
      const highValue = direct ? highest : d(1).div(highest).toString()
      _min = formatPrice(lowValue, 6)
      _max = formatPrice(highValue, 6)
    } else {
      _min = '-'
      _max = '-'
    }
    setMinPriceForDate(_min)
    setMaxPriceForDate(_max)
  }, [direct, tab.key, poolRangeObj, currentPosBaseInfo])

  const ranges = useMemo(() => {
    if (isAvailableObject(currentPosPoolsRelatedData) && isAvailableObject(posPoolInfo) && isAvailableObject(poolRangeObj)) {
      const currentRangeObj = poolRangeObj[currentPosBaseInfo?.clmmPool as string]
      if (currentRangeObj) {
        const ranges = currentRangeObj?.ranges.reduce((acc: any, item: any) => {
          acc[item.dateType] = item
          return acc
        }, {})
        return ranges
      }
    }
  }, [currentPosPoolsRelatedData, posPoolInfo, currentPosBaseInfo, tab?.key, poolRangeObj])
  // const isActive = currentPosPoolsRelatedData?.currentStatus == 'Active'
  const { getPosIsActive } = usePosHelper()
  const isActive = useMemo(() => {
    return getPosIsActive(currentPosBaseInfo as PosBaseInfo, posPoolsOriginalData?.[currentPosBaseInfo?.clmmPool || '']?.current_sqrt_price)
  }, [currentPosBaseInfo, posPoolsOriginalData])

  const { getTokenAmountValue } = useTokenPrice()
  const currentPosLiquidity = posLiquidityData[currentPosBaseInfo?.posId as string]
  const amountValueA = getTokenAmountValue(currentPosBaseInfo?.displayTokenA?.coin_type, currentPosLiquidity?.displayCoinAmountA)
  const amountValueB = getTokenAmountValue(currentPosBaseInfo?.displayTokenB?.coin_type, currentPosLiquidity?.displayCoinAmountB)
  const currentPosTvl = useMemo(() => {
    return d(amountValueA).plus(amountValueB).toString()
  }, [amountValueA, amountValueB])

  return (
    <VStack w="100%" align="flex-start" p={{ base: '12px 8px', lg: '20px' }} bg="bg_secondary" borderRadius="16px">
      <HStack w="100%" justify="space-between">
        <Text color="text_caption" fontSize="16px">
          Price range
        </Text>
        {rangeTabList?.length > 0 && (
          <SelectTab<any, any>
            type="outlineTab"
            tabList={rangeTabList}
            currentTab={currentRangeTab}
            handleChangeTab={tab => onReverseClick(tab)}
            wrapStyle={{
              h: '32px',
              p: '3px',
              border: '1px solid',
              borderColor: 'border',
              borderRadius: '8px',
              gap: '4px',
              zIndex: '99'
            }}
            itemStyle={{
              h: '24px',
              p: '4px 8px',
              borderRadius: '4px',
              gap: '4px'
            }}
          />
        )}
      </HStack>
      <HStack
        gap={{ base: '20px', lg: '40px' }}
        w="100%"
        align="flex-end"
        justify="space-between"
        p="8px 0"
        flexDirection={{ base: 'column', lg: 'row' }}
      >
        <VStack w={{ base: '100%', lg: 'unset' }} align="center" justify="center" pb="2px">
          <ChartBlock direct={direct} minPriceForDate={minPriceForDate} maxPriceForDate={maxPriceForDate} />
          <HStack>
            <Center borderRadius="4px" border="1px solid" borderColor="border" w="20px" h="20px" color="text_caption">
              <Divider w="14px" border="1px dashed" />
            </Center>
            <Text color="primary_gray" fontSize="12px">
              {tab?.type} Price Range
            </Text>
          </HStack>
          <Text color="text_caption" textAlign="center" fontSize="12px" lineHeight="12px">
            {direct ? minPriceForDate : maxPriceForDate}&nbsp;-&nbsp;
            {direct ? maxPriceForDate : minPriceForDate}
          </Text>
        </VStack>
        <VStack w={{ base: '100%', lg: 'calc(100% - 220px)' }} gap={{ base: '16px', lg: '20px' }}>
          <HStack mt="8px" align={{ base: 'center', lg: 'flex-start' }}>
            <Text color="primary_gray">Current Price</Text>
            <PositionCurrentPrice
              posId={currentPosBaseInfo?.posId || ''}
              displayTokenA={currentPosBaseInfo?.displayTokenA}
              displayTokenB={currentPosBaseInfo?.displayTokenB}
              isChangeDirect={isChangeDirect}
              handleDirect={onReverseClick}
              haveChangeIcon={false}
            />
          </HStack>
          <HStack w="100%" bg="card_bg" borderRadius="12px" p="16px 20px" justify="space-between">
            <VStack w="50%">
              <Text color="primary_gray" fontSize="12px">
                Min Price
              </Text>
              <Skeleton isLoaded={!!currentPosPoolsRelatedData?.minPrice && !!currentPosPoolsRelatedData?.minPriceResever}>
                <Text color="text_caption" whiteSpace="nowrap">
                  {isChangeDirect || isChangeDirect == undefined
                    ? `${currentPosPoolsRelatedData?.minPrice} `
                    : `${currentPosPoolsRelatedData?.minPriceResever}`}
                </Text>
              </Skeleton>
              <Text color="primary_gray" fontSize="12px">
                {perText}
              </Text>
            </VStack>
            <VStack w="50%">
              <Text color="primary_gray" fontSize="12px">
                Max Price
              </Text>
              <Skeleton isLoaded={!!currentPosPoolsRelatedData?.maxPrice && !!currentPosPoolsRelatedData?.maxPriceResever}>
                <Text color="text_caption" whiteSpace="nowrap">
                  {isChangeDirect || isChangeDirect == undefined
                    ? ` ${currentPosPoolsRelatedData?.maxPrice}`
                    : `${currentPosPoolsRelatedData?.maxPriceResever}`}
                </Text>
              </Skeleton>
              <Text color="primary_gray" fontSize="12px">
                {perText}
              </Text>
            </VStack>
          </HStack>
          <HStack w="100%" flexDirection={{ base: 'column', lg: 'row' }} justify="space-between">
            {/* {showNewVersionApr && (
              <EstimatedApr
                tab={tab}
                showFarmingApr={false}
                setTab={setTab}
                ranges={ranges}
                currentPosPoolsRelatedData={currentPosPoolsRelatedData}
                posPoolInfo={posPoolInfo}
                isActive={isActive}
                flexDirection="row"
                isFarms={currentPosBaseInfo?.posType == 'farms'}
                currentPosTvl={currentPosTvl}
                isPosition={true}
              />
            )} */}
            {showNewVersionApr && (
              <WithTooltipInfo
                label="APR"
                tooltip="APR based on the daily yield accrued by this position. Past performance is not indicative of future results. Calculations are an estimate and only for reference."
                wrapStyle={{
                  flexDir: { base: 'row', lg: 'column' },
                  align: { base: 'center', lg: 'flex-start' },
                  width: { base: '100%', lg: 'unset' },
                  gap: '2px'
                }}
              >
                <Skeleton isLoaded={!!currentPosPoolsRelatedData} h="14px">
                  <Text color="primary">{positionApr ?? '--'}</Text>
                </Skeleton>
              </WithTooltipInfo>
            )}

            <WithTooltipInfo
              label="Leverage"
              tooltip="This parameter indicates the concentration rate of your liquidity relative to a full range position."
              wrapStyle={
                showNewVersionApr
                  ? {
                      flexDir: { base: 'row', lg: 'column' },
                      align: { base: 'center', lg: 'flex-end' },
                      width: { base: '100%', lg: 'unset' },
                      gap: '2px'
                    }
                  : {
                      flexDir: { base: 'row', lg: 'column' },
                      align: { base: 'center', lg: 'center' },
                      justify: { base: 'space-between', lg: 'center' },
                      mt: '-4px',
                      width: { base: '100%', lg: '100%' },
                      gap: '8px'
                    }
              }
            >
              <Skeleton isLoaded={!!leverage && !!currentPosPoolsRelatedData}>
                <Text color="primary">{leverage}</Text>
              </Skeleton>
            </WithTooltipInfo>
          </HStack>
        </VStack>
      </HStack>
    </VStack>
  )
}
export default PositionChartInfo
